import React, { Component } from 'react';
import styled from 'styled-components';


class ArtHeader extends Component {
    Border = styled.div`
    margin-top: 5px;
    width: 20%;
    transition: .3s;
    border-top: ${this.props.class} 2px solid;
    `
    Wrapper = styled.header`
    margin: 30px;
    width: 110px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: uppercase;

    h3 {
        font-size: 2em;
    }

    :hover ${this.Border} {
        width: 100%;
    }

`
    render() {
        return (
            <this.Wrapper>
                <h3>{this.props.title}</h3>
                <this.Border className={this.props.class} />
            </this.Wrapper>
        )
    }
}

export default ArtHeader