import React from 'react';
import styled from 'styled-components';

import Item from './Item';

const MENU = [
    {
        route: '#',
        title: 'home'
    }, {
        route: '#',
        title: 'services'
    }, {
        route: '#',
        title: 'our work'
    }, {
        route: '#',
        title: 'about us'
    }, {
        route: '#',
        title: 'contact'
    }, {
        route: '/book/',
        title: 'book online'
    }
]

const Wrapper = styled.nav`
    display: flex;
    justify-content: center;
    width: 100%;
    position: fixed;
    z-index: 99;
    top: 0;
    background: #2f2e2e;
    justify-content: center;
    border-bottom: #949494 solid 2px;;
    text-transform: uppercase;
    font-size: 1.5em;
    padding:  13px 0;
`


const Navbar = () => {
    return (
        <Wrapper>
            {MENU.map((item, index) => (
                <Item
                    key={`${item.id}-${index}`}
                    exact
                    route={item.route}
                    title={item.title}
                />
            ))}
        </Wrapper>
    )
}

export default Navbar;