import React from 'react';
import styled from 'styled-components';

const MENU = [
    {
        route: '#',
        title: 'fab fa-facebook-f'
    }, {
        route: '#',
        title: 'fab fa-pinterest-p'
    }, {
        route: '#',
        title: 'fab fa-instagram'
    }
]

const Wrapper = styled.div`
    display: flex;
`

const Item = styled.a`
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    margin-bottom: 50px;
    font-size: 2em;
    width: 25px;
    height: 25px;
    color: #2f2e2e;
    background: #eee;
    padding: 10px;
    border-radius: 100%;
    transition: .3s;
    text-decoration: none;
    :hover {
        background: #ddd;
    }
`


const Navbar = () => {
    return (
        <Wrapper>
            {MENU.map((item, index) => (
                <Item
                    key={`${item.id}-${index}`}
                    exact
                    href={item.route}
                >
                    <i className={item.title}></i>
                </Item>
            ))}
        </Wrapper>
    )
}

export default Navbar;