import React, { Component } from 'react';
import styled from 'styled-components';

const LinkBorder = styled.div`
    width: 0%;
    border-top: #eee 2px solid;
    position: absolute;
    right: 0;
    transition: .3s ease;
`

const Link = styled.a`
    margin: 0 7px;
    padding: 8px 8px;
    display: inline-block;
    border-radius: 0.3rem;
    position: relative;
    background: none;
    color: #eee;
    font-weight: 800;
    transition: 0.2s;
    text-decoration: none;

    :hover, :active {
        color: #ccc;

        ${LinkBorder} {
            width: 100%;
            left: 0;
        }
    }
`

class Item extends Component {
    render() {
        return (
            <Link href={this.props.route}>
                <span>{this.props.title}</span>
                <LinkBorder />
            </Link>
        )
    }
}

export default Item;